import { MORE_DATA } from "../reducers/reducersKeys";
import { getParameter, getQueryString } from "../utils";
import { isSocialWall, isTagShop, tagembed } from "./api";
export let dataRegion;

export const isUpdateFrom = /.*widget(-test)?\.tagembed.*/.test(window.location.href) ? { userfrom: tagembed } : { userfrom: "taggbox" }

export const getHashTagParam = (rID) => {
    const parsed = window.location.href;
    const updateContainerID = rID ? `#co_${rID}` : ".taggbox";
    let shopParams = {};
    let headerTags = {};

    
    const param = (key) => getQueryString(parsed, key);
    const region = param('region')
    const web = param('website')
    const tag = param('tags')

    if (isTagShop && (/.*(test\.tagshop\.ai\/w\/|widget-test\.tagshop\.ai|widget\.tagshop\.ai).*/.test(window.location.host) || web || region)) {
        shopParams = param('wall') ? {} : { Website: 1 };
        if (region) dataRegion = region;
    }
        
    if (isSocialWall && param('wallKey')) shopParams.wallKey = param('wallKey');
         headerTags = tag
        ? {
            tag: tag.includes("true") ? 1 : 0,
            keywords: decodeURIComponent(param('data-keywords') || param('keywords') || '')
            }
        : web
        ? { Website: 1, ...(dataRegion && { region: dataRegion }) }
        : (() => {
            const taggbox = document.querySelector(updateContainerID);
            if (!taggbox) return {};

            const { keywords, source, region, tags } = taggbox.dataset;

            return {
                ...(tags && {
                tag: tags.includes("true") ? 1 : 0,
                keywords: decodeURIComponent(keywords || '')
                }),
                ...(isTagShop && {
                Website: source ? 1 : undefined,
                region: region || undefined
                })
            };
            })();

    return { headers: { ...shopParams, ...headerTags, ...isUpdateFrom }};
}


export const getContainer = (ID) => document.querySelector(ID ? `#co_${ID}` : ".taggbox");
export const getThemeId = (ID) => getContainer(ID)?.getAttribute("theme-id") || getParameter("theme-id") || "";

export const getAttributes = (ID) => {
    const filterType = getContainer(ID)?.getAttribute("data-filter-type");
    return {
        isStatus: !!filterType,
        mUrl: filterType ? `${filterType}/${encodeURIComponent(getContainer(ID)?.getAttribute("data-filter-id"))}` : "",
        dataFilterType: filterType && (window.dataFilterType = filterType),
    };
};



export const dispatchMoreData = (dispatch, newData, networkId, appendData, tagName, postCount = 0, hasPost = 1) => {
    dispatch({
        type: MORE_DATA,
        payload: {
            newData,
            filterNetworkId: networkId,
            appendData: { ...appendData },
            tagName,
            hasMoreData: {
                [networkId]: {
                    hasMoreData: Object.keys(newData).length >= postCount && hasPost === 1,
                    after: appendData.after,
                },
            },
        },
    });
};